import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Login from '../pages/Login'
import Home from '../pages/Home'
import cookie from '../utils/cookie'

 const router = new VueRouter({
    routes:[
      {
          path:'/',
          redirect:'/login'
      },
      {//登录
          path:'/login',
          component:Login
      },
      {//主页
        path:'/index',
        component:Home
      }
    ]
})
export default router