        </div> -->
      </div>
      <!-- 表格数据 -->
      <div class="flex-list" style="justify-content:left;">
        <div class="role-div" v-for="item in roleOptions" :key="item.id" @click="editRoleFun($event, item)">
          
          <div class="role-img">
            <div class="favorite-edit">
              <i class="el-icon-edit"></i>
            </div>
          </div>
          <div class="role-main-name">{{item.roleName  }}</div>
        </div>
      </div>
      
      <!-- <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pageNum"
          background
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div> -->
    </div>
    <!-- 角色 -->
    <div class="role-manage" :style="'right:' + roleTabShow + 'px'">
      <div class="box-title">
        <div class="box-title-font">角色</div>
        <div class="close-btn" @click="roleTabShow = '-350'"></div>

      </div>
      <!-- { leftOut: (activeRole.id = item.id) }, -->
      <ul>
        <li
