"use strict";

import {clearStore} from './locastorage';
import axios from "axios";
import JSONBIG from 'json-bigint'
import {Message} from 'element-ui';
import { Loading } from 'element-ui';

// baseURL: 'http://localhost:9010',
//baseURL: 'http://10.1.100.84:9999/metedisaster'
var service = axios.create({ 
    baseURL: '',
    withCredentials: true, // 跨域请求时发送Cookie
    cancelRequest: true,
    timeout: 1000*3,
    transformResponse:[data=>{
      //data是原始字符串数据
      const jsonObject = JSONBIG.parse(data)
      console.log(jsonObject,'yjy')
      return jsonObject 
      }]

});
let loadingInstance
service.interceptors.request.use(
    function (config) {
        
        return config;
    },
    function (error) {
        // Do something with request error
        return Promise.reject(error);
    }
);

// Add a response interceptor
service.interceptors.response.use(
    function (response) {
        if (loadingInstance) {
            loadingInstance.close()
        }
        if (response.data.code == 0 && response.status == 200) {
             //如果有加载中组件，则关闭
            
        }
        else{
            // error
        }
        return response;
    },
    function (error) {
        if (loadingInstance) {
            loadingInstance.close()
        }
        if (error.response) {
            if (error.response.data.code == 10000 || error.response.data.code == 10001 || error.response.data.code == 10002) {
                clearStore();

            }
        }
        return error;
    }
);


export default {
    get(url, data, header) {
        return new Promise((resolve) => {
            //全局loading
            loadingInstance = Loading.service({ fullscreen: true,background: 'rgba(0, 0, 0, 0.7)',text: '请稍后' })
            service.get(url, {
                data: data
            }, header).then(res => {
                if(res.data.code==9000){
                  //this.$message.error('登录失效，请重新登录');
                  this.$router.replace({path:'/login'})
                }
                resolve(res)
            }, err => {
                if (err.response) {
                    Message({
                        showClose: true,
                        message: err.response.data.message,
                        type: 'error'
                    });
                }
            })
        })
    },
    post(url, data, header) {
        return new Promise((resolve) => {
            //全局loading
            loadingInstance = Loading.service({ fullscreen: true,background: 'rgba(0, 0, 0, 0.7)',text: '请稍后' })
            service.post(url, data, header).then(res => {
              if(res.data.code==9000){
                //this.$message.error('登录失效，请重新登录');
                this.$router.replace({path:'/login'})
              }
                resolve(res);

            }, err => {
                if (err.response) {
                    Message({
                        showClose: true,
                        message: err.response.data.message,
                        type: 'error'
                    });
                }
            })
        })
    },
    postNL(url, data, header) {
      return new Promise((resolve) => {
          service.post(url, data, header).then(res => {
            if(res.data.code==9000){
              this.$message.error('登录失效，请重新登录');
              this.$router.replace({path:'/login'})
            }
            resolve(res);

          }, err => {
              if (err.response) {
                  Message({
                      showClose: true,
                      message: err.response.data.message,
                      type: 'error'
                  });
              }
          })
      })
  },
    put(url, data, header) {
        return new Promise((resolve) => {
            //全局loading
            loadingInstance = Loading.service({ fullscreen: true,background: 'rgba(0, 0, 0, 0.7)',text: '请稍后' })
            service.put(url, data, header).then(res => {
              if(res.data.code==9000){
                this.$message.error('登录失效，请重新登录');
                this.$router.replace({path:'/login'})
              }
                resolve(res);

            }, err => {
                if (err.response) {
                    Message({
                        showClose: true,
                        message: err.response.data.message,
                        type: 'error'
                    });
                }
            })
        })
    },
    delete(url, data, header) {
        return new Promise((resolve) => {
            //全局loading
            loadingInstance = Loading.service({ fullscreen: true,background: 'rgba(0, 0, 0, 0.7)',text: '请稍后' })
            service.delete(url, data, header).then(res => {
              if(res.data.code==9000){
                this.$message.error('登录失效，请重新登录');
                this.$router.replace({path:'/login'})
              }
                resolve(res);
            }, err => {
                if (err.response) {
                    Message({
                        showClose: true,
                        message: err.response.data.message,
                        type: 'error'
                    });
                }
            })
        })
    },
    Lpost(url, data, header) {
        return new Promise((resolve) => {
            //全局loading
            loadingInstance = Loading.service({ fullscreen: true,background: 'rgba(0, 0, 0, 0.7)',text: '请稍后' })
            service.post(url, data,header)
            .then(res => {
              if(res.data.code==9000){
                this.$message.error('登录失效，请重新登录');
                this.$router.replace({path:'/login'})
              }
                resolve(res);

            }, err => {
                if (err.response) {
                    Message({
                        showClose: true,
                        message: err.response.data.message,
                        type: 'error'
                    });
                }
            })
        })
    },
    download(url, data, header) {
        return new Promise((resolve, reject) => {
            axios({
              method: 'get',
              url: url,
              data: data,
              responseType: 'blob', // 注意返回的数据格式
              headers:header
            }).then(res => {
              if(res.data.code==9000){
                this.$message.error('登录失效，请重新登录');
                this.$router.replace({path:'/login'})
              }
              resolve(res)
            }).catch(error => {
              reject(error)
            })
          })

    },
    postDownload(url, data, header) {
      return new Promise((resolve, reject) => {
          axios({
            method: 'post',
            url: url,
            data: data,
            responseType: 'blob', // 注意返回的数据格式
            headers:header
          }).then(res => {
            if(res.data.code==9000){
              this.$message.error('登录失效，请重新登录');
              this.$router.replace({path:'/login'})
            }
            resolve(res)
          }).catch(error => {
            reject(error)
          })
        })
    },
}
